<template>
<section>
  <div id="share">
    <img src="../assets/imgs/share-bg.png" alt="">
    <div id="qrcode"><img :src="imgUrl" alt=""></div> 
  </div>
</section>
</template>

<script>
import { getDevUrl } from "../assets/js/env";
import axios from "axios";
import QRCode from "qrcode";
export default {
  data() {
    return {
      sessionKey:'',
      imgUrl: "",
      url : '',
    };
  },
  methods: {
    //获取URL里参数
    getUrlParam(name, noDecode, url) {
      let re = new RegExp("[?#&]" + name + "=([^&#]*)", "i");
      let _url = url || window.location.href;
      let m = re.exec(_url);
      let ret = m ? m[1] : "";
      ret = ret.replace(/[+]/gi, "%20");
      return !noDecode ? decodeURIComponent(ret) : ret;
    },
    qrcode() {
      console.log(this.url)
      QRCode.toDataURL(this.url)
        .then(url => {
          this.imgUrl = url;
        })
        .catch(err => {
          console.error(err);
        });
    }
  },
  mounted() {
    this.sessionKey = this.getUrlParam("sessionKey");
    this.url =  getDevUrl("/config/menu/"+this.sessionKey);
    // this.url =  "http://iot.1000mob.com/dev/config/menu/"+this.sessionKey;
    this.qrcode();
  }
};
</script>

<style lang='scss' scoped>
@import "../assets/mixin.scss";
section{
  height: 100%;
}
#share {
  position: relative;
  background-color: #fff;
  height: 100%;
  img {
    width: 100%;
  }
}
#qrcode{
  width: ptr(260);
  position: absolute;
  right: ptr(70);
  top:ptr(440);
  >img{
    display: block;
    width: 100%;
    border-radius: ptr(80);
  }
}
.toast-reast {
  min-height: auto;
  @include lh(100);
}
</style>




